.imgBody{
  overflow: hidden;
  border-radius: 100%;
  width: 50px;
  height: 50px;
}

.imgBody img{
  height: 50px;
}

.msgBody{
  width:300px;
  min-height: 50px;
  background: #FFFFFF;
  border-radius: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px 20px;
  margin: 0px 10px;
}

.msgContainer, .selfMsgContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 15px 0;
}

.selfMsgContainer .msgBody{
  -ms-flex-order:1;
      order:1;
}

.selfMsgContainer .imgBody{
  -ms-flex-order:2;
      order:2;
}



