.signupBody{
  position: absolute;
  top:35%;
  width:300px;

}

  .signupCardHeader{
    margin-bottom: 15px;
  }

  .signupCardContentBody{

  }
    .inputContainer{
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: row;
          flex-direction: row;
    }
    .authWarningText{
      color:red;
      display: none;
    }

    .inputContainer{
      margin-bottom: 10px;
    }

      .input-group-addon{
        background:#FFF;
        border-right: 0;
      }

      input.form-control{
        border-left: 0;

      }

      .inputContainer.fileUpload{
        display:-ms-flexbox;
        display:flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        margin-bottom: 15px;
      }

        .profileImgBox{
          width:50%;
          margin-bottom: 10px;
          padding: 10px 10px;
          display:none;
          -ms-flex-pack: center;
              justify-content: center;
          -ms-flex-align: center;
              align-items: center;
        }
        .profileImgBox .imgProfileRemove{
          display:none;
          position: absolute;
          top:5px;
          right:70px;
          color:black;
        }

          .inputContainer.fileUpload .uploadedProfileImageReg{
            opacity:0;
            border-radius: 50%;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-pack: center;
                justify-content: center;
            -ms-flex-align: center;
                align-items: center;
            width:100px;
            height: 100px;
            overflow: hidden;

          }

            .inputContainer.fileUpload .uploadedProfileImageReg img{
              width:100%;
            }

        .inputContainer.fileUpload .uploadButton{
          width:100%;
          display: -ms-flexbox;
          display: flex;
          -ms-flex-direction: row;
              flex-direction: row;
          -ms-flex-pack: center;
              justify-content: center;
          -ms-flex-align: center;
              align-items: center;

        }

        .inputContainer.fileUpload .uploadButton i{
          margin-right: 5px;
        }


  .signupCardFooter{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;

  }
    .signupCardFooter button:last-of-type{
      margin-left: 10px;
    }


/* --------------------------
  Keyframes
--------------------------- */
@-webkit-keyframes grow {
	0% {
		-webkit-transform: scale(.3);
		        transform: scale(.3);
    opacity: 0;
	}
	50% {
		-webkit-transform: scale(1.15);
		        transform: scale(1.15);
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@keyframes grow {
	0% {
		-webkit-transform: scale(.3);
		        transform: scale(.3);
    opacity: 0;
	}
	50% {
		-webkit-transform: scale(1.15);
		        transform: scale(1.15);
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

/*------------
Anamations
-----------------*/
.inputContainer.fileUpload .uploadedProfileImageReg{
    -webkit-animation-name: grow;
            animation-name: grow;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
}

.inputContainer.fileUpload .uploadedProfileImageReg.growIn{
  opacity:1;
}



